import React from 'react';
import s from './Users.module.css';
import Preloader from '../../common/preloader/Preloader';
import Paginator from "./Paginator";
import User from "./User";

let Users = ({totalUsersCount, pageSize, currentPage, onPageChanged, isPreloader, users, followingInProgress, unfollowThunk, followThunk, ...props}) => {
    return (
        isPreloader ? <Preloader/> :
            <div className={s.content}>
                <Paginator totalItemsCount ={totalUsersCount}
                           pageSize={pageSize}
                           currentPage={currentPage}
                           onPageChanged={onPageChanged}
                />

                {users.map(u => <User key={u.id} user={u} followingInProgress={followingInProgress} unfollowThunk={unfollowThunk} followThunk={followThunk}  /> )}
            </div>
    )

}
export default Users;