import {userAPI} from "./axios/requestApi";
import {updateObjectInArray} from "../utils/helpers";

const FOLLOW = 'users/FOLLOW';
const UNFOLLOW = 'users/UNFOLLOW';
const SET_USERS = 'users/SET_USERS';
const SET_CURRENT_PAGE = 'users/SET_CURRENT_PAGE';
const SET_TOTAL_USERS_COUNT = 'users/SET_TOTAL_USERS_COUNT';
const TOGGLE_IS_FETCHING = 'users/TOGGLE_IS_FETCHING';
const FOLLOWING_IN_PROGRESS = 'users/FOLLOWING_IN_PROGRESS';

const initialState = {
    users: [],
    totalUsersCount: 0,
    pageSize: 5,
    currentPage: 1,
    isPreloader: false,
    followingInProgress: []

}

const usersReducer = (state = initialState, action) => {
    switch (action.type) {

        case FOLLOW: {
            return {
                ...state,
                users: updateObjectInArray(state.users, action.userID, "id",  {followed: true})
            }
        }
        case UNFOLLOW: {
            return {
                ...state,
                users: updateObjectInArray(state.users, action.userID, "id",  {followed: false})
            }
        }
        case SET_USERS: {
            return {
                ...state,
                users: [...action.usersList]
            }
        }
        case SET_CURRENT_PAGE: {
            return {
                ...state,
                currentPage: action.currentPage
            }
        }
        case SET_TOTAL_USERS_COUNT: {
            return {
                ...state,
                totalUsersCount: action.totalCount
            }
        }
        case TOGGLE_IS_FETCHING: {
            return {
                ...state,
                isPreloader: action.isPreloader
            }
        }
        case FOLLOWING_IN_PROGRESS: {
            return {
                ...state,
                followingInProgress: action.followingProgress ?
                    [...state.followingInProgress, action.userID] :
                    [...state.followingInProgress.filter(id => id !== action.userID)]
            }
        }
        default:
            return state;
    }

}

export const follow = (idUser ) => {
    return {
        type: FOLLOW,
        userID: idUser,
    }
}
export const unfollow = (idUser ) => {
    return {
        type: UNFOLLOW,
        userID: idUser,
    }
}
export const setUsers = (users) => {
    return {
        type: SET_USERS,
        usersList: users
    }
}
export const setCurrentPage = (currentPage) => {
    return {
        type: SET_CURRENT_PAGE,
        currentPage: currentPage
    }
}
export const setTotalCount = (totalUsersCount) => {
    return {
        type: SET_TOTAL_USERS_COUNT,
        totalCount: totalUsersCount
    }
}
export const setPreloader = (isPreloader) => {
    return {
        type: TOGGLE_IS_FETCHING,
        isPreloader
    }
}
export const setFollowingInProgress = (followingProgress, userID) => {
    return {
        type: FOLLOWING_IN_PROGRESS,
        followingProgress,
        userID
    }
}

export const getUsersThunkCreator = (currentPage, pageSize) => {
    return async (dispatch) => {
        dispatch(setPreloader(true));
        dispatch(setCurrentPage(currentPage));

        let data = await userAPI.setNewUsers(currentPage, pageSize)

        dispatch(setUsers(data.items));
        dispatch(setTotalCount(data.totalCount));
        dispatch(setPreloader(false));
    }
}
const followUnfollowFlow = async (dispatch, userID, apiMethod, actionCreator) => {

    dispatch(setFollowingInProgress(true, userID));
    let response = await apiMethod(userID)

    if (response.data.resultCode === 0) {
        dispatch(actionCreator(userID))
    }
    dispatch(setFollowingInProgress(false, userID));
    }

export const followThunk = (userID) => {
    return async (dispatch) => {
        followUnfollowFlow(dispatch, userID, userAPI.follow.bind(userID), follow)
    }
}

export const unfollowThunk = (userID) => {
    return async (dispatch) => {
        followUnfollowFlow(dispatch, userID, userAPI.unfollow.bind(userID), unfollow)
    }
}
export default usersReducer;