import React, {useEffect} from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { getCurrentPage, getFollowingInProgress, getIsPreloader, getPageSize, getTotalUsersCount, getUsersSelector } from 'redux/users-selector';
import { followThunk, unfollowThunk, setUsers, setCurrentPage, setTotalCount, getUsersThunkCreator } from '../../../redux/Users-Reducer';
import Users from './Users';

const UsersContainer = (props) => {

    useEffect(() =>{
        props.getUsersThunkCreator(props.currentPage, props.pageSize);
    }, [props.currentPage])

    let onPageChanged = (pageNumber) => {
        props.getUsersThunkCreator(pageNumber, props.pageSize)
    }

        return <Users   onPageChanged = {onPageChanged}
        totalUsersCount = {props.totalUsersCount}
        pageSize ={props.pageSize}
        currentPage = {props.currentPage}
        users = {props.users}
        isPreloader = {props.isPreloader}
        followingInProgress = {props.followingInProgress}
        unfollowThunk = {props.unfollowThunk}
        followThunk = {props.followThunk}
        />

}
let mapStateToProps = (state) => {
    return {
        users: getUsersSelector(state),
        pageSize: getPageSize(state),
        totalUsersCount: getTotalUsersCount(state),
        currentPage: getCurrentPage(state),
        isPreloader: getIsPreloader(state),
        followingInProgress: getFollowingInProgress(state),
    }
}

let mapDispatchToProps = {
        setUsers,
        setCurrentPage,
        setTotalCount,
        getUsersThunkCreator,
        followThunk,
        unfollowThunk,
}


export default compose(
    connect(mapStateToProps, mapDispatchToProps)
)(UsersContainer);